import React, { Component } from 'react';
import SamplesRow from './cluster_helpers/SamplesRow';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';
import Button from 'react-bootstrap/Button';
import {makeHSVMonochromeData} from "./monochrome_helpers/MonochromeUtils";

import 'bootstrap/dist/css/bootstrap.min.css';


class MonochromeIdentification extends Component {
  constructor(props) {
    super(props)
    this.state = {
      images: makeHSVMonochromeData() //each row [ not black & white, black and white]
    }
  }

  createAllRows = () => {
      let allSlides = []
      let myStyle = {
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: '50% 50%',
            display: 'inline-block',
            height: '100%',
            width: '100%',
            height: '200px',
            overflow: 'hidden',
            justifyContent: 'center',
            textAlign: 'center'};
      // Outer loop to create parent
      for (let i = 0; i < this.state.images.length; i+=1) {
        allSlides.push(
          <div style ={ myStyle }>
            <SamplesRow samples={this.state.images[i]}  />
          </div>)
      }
      return allSlides
    }

  render() {
    return (
    <div style ={ {  justifyContent: 'center'  } }>

      <div style={{  height: '100%',  width: '100%'}}>
        <div  style ={{
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: '50% 50%',
          display: 'inline-block',
          height: '100%',
          width: '100%',
          height: '20px',
          overflow: 'hidden',
          justifyContent: 'center',
          textAlign: 'center'
        }}>
          <div className="slide" style={{backgroundPosition: '50% 60%',  display: 'inline-block',  height: '20px',  width: '200px'}}>Monochrome</div>
          <div style={{width: '10px',   display: 'inline-block',}}></div>
          <div className="slide" style={{ backgroundPosition: '50% 60%',  display: 'inline-block', height: '20px',  width: '200px'}}>Colored</div>
          <div style={{width: '10px',   display: 'inline-block',}}></div>
        </div>
      </div>

      {this.createAllRows()}

    </div>
    );
  }

  }

  export default MonochromeIdentification;
